<template>
  <div class="gcodeManner">
    <div class="container-main">
      <!-- banner -->
      <div class="head-show">
        <div class="left-feature">
          <i class="iconfont spe-icon">&#xe656;</i>
          <span class="feature-p"> Gcode</span>
        </div>
      </div>
      <!-- :  -->
      <div class="main-container">
        <!-- Gcode -->
        <div class="left-container">
          <el-slider v-model="state.scaleVal" vertical height="515px" />
        </div>
        <!-- Gcode -->
        <div
          id="middle-container"
          v-loading="state.loading"
          element-loading-text="gcode..."
          :element-loading-spinner="svg"
          element-loading-svg-view-box="-10, -10, 50, 50"
          element-loading-background="rgba(255, 255, 255, 0.8)"
        ></div>
        <div class="right-container">
          <p class="gcode-name">{{ state.gcodeName }}</p>
          <!--  -->
          <div class="bottom-box">
            <p>
              : <span>{{ state.formatSlice.length }}</span>
            </p>
            <p>
              : <span>{{ state.layer.layerCount }}</span>
            </p>
          </div>
          <!--  -->
          <div class="bottom-btn">
            <div class="btn">
              <el-upload
                :show-file-list="false"
                class="upload-demo"
                :limit="1"
                :action="state.fileUrl"
                :before-upload="handleUpload"
                :on-success="uploadSuccess"
                :on-change="handleChange"
              >
                <el-button size="small"
                  ><i class="iconfont" style="font-size: 12px">&#xe643;</i
                  ></el-button
                >
              </el-upload>
            </div>
          </div>
          <!--  -->
          <div class="bottom-btn">
            <el-button></el-button>
          </div>
        </div>
      </div>
      <div class="bottom-container">
        <div class="left-control">
          <span class="spe-span">: </span>
          <!--  -->
          <div class="slide-con">
            <el-slider
              v-model="state.layer.layerCount"
              :min="state.layer.min"
              :max="state.layerCountMax"
              :step="state.layer.step"
            />
          </div>
          <div class="brInfo">{{ state.layer.layerCount }}</div>
        </div>
        <div class="left-control">
          <span class="spe-span">: </span>
          <!--  -->
          <div class="slide-con">
            <el-slider
              v-model="state.step.stepCount"
              :min="state.step.min"
              :max="state.stepCountMax"
              :step="state.step.step"
            />
          </div>
          <div class="brInfo">{{ state.step.stepCount }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import {
  getCurrentInstance,
  onMounted,
  onUnmounted,
  reactive,
  watch,
} from "vue";
import GcodeLoader from "@/utils/gcode/gcodeScene";
import { ElMessage } from "element-plus";
const { proxy } = getCurrentInstance();
const svg = `
        <path class="path" d="
          M 30 15
          L 28 17
          M 25.61 25.61
          A 15 15, 0, 0, 1, 15 30
          A 15 15, 0, 1, 1, 27.99 7.5
          L 15 15
        " style="stroke-width: 4px; fill: rgba(0, 0, 0, 0)"/>
      `;
let state = reactive({
  // ()
  runFlag: false,
  isGcode: true,
  loading: false,
  layer: {
    layerCount: 1,
    min: 0,
    max: 100,
    step: 1,
  },
  fileUrl: "http://127.0.0.1:80",
  gcodeUrl: "",
  step: {
    stepCount: 0,
    min: 0,
    max: 100,
    step: 1,
  },
  scaleVal: 80,
  gcodeName: "",
  gcodeLoader: null,
  // Gcode
  formatSlice: [],
  // 
  layerCountMax: 0,
  // 
  stepCountMax: 0,
});
// : (),gcode
watch(
  () => state.layer.layerCount,
  (val) => {
    //   
    // ,
    if (val > proxy.layerCountMax) {
      return;
    }
    if (!proxy.gcodeLoader) {
      return;
    }
    console.log("", state.formatSlice.length);
    // 
    const layerInnerStepStart = state.formatSlice[val - 1];
    const layerInnerStepEnd = state.formatSlice[val];
    proxy.layerInnerStepStart = layerInnerStepStart;
    proxy.layerInnerStepEnd = layerInnerStepEnd;
    //  + 
    state.stepCountMax = state.step.stepCount =
      layerInnerStepEnd - layerInnerStepStart;
    proxy.gcodeLoader.changeUniformsStatus(
      layerInnerStepStart,
      layerInnerStepEnd
    );
  }
);
watch(
  // 
  () => state.step.stepCount,
  (val1) => {
    console.log("", val1);
    proxy.gcodeLoader.changeUniformsStatus(
      proxy.layerInnerStepStart,
      proxy.layerInnerStepStart + val1
    );
  }
);
const handleChange = (file) => {
  console.log("", file.name);
  state.gcodeName = file.name;
  // ,,,,
  if (!state.runFlag && state.isGcode) {
    state.loading = true;
    console.log("", state.gcodeUrl, state.runFlag);
    state.gcodeUrl = URL.createObjectURL(file.raw);
    proxy.gcodeLoader.renderFromUrl(state.gcodeUrl);
    state.runFlag = true;
  }
};
const handleUpload = (file) => {
  const ext = file.name.substring(file.name.lastIndexOf(".") + 1);
  state.isGcode = true;
  if (ext.toLowerCase() !== "gcode") {
    ElMessage.warning("gcode!");
    state.isGcode = false;
    return;
  }
};
const uploadSuccess = () => {
  ElMessage.success("");
};
// 
onUnmounted(() => {
  proxy.gcodeLoader.cleanAll();
  proxy.gcodeLoader = null;
});
onMounted(() => {
  console.log("axios", proxy.$axios);
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  // 
  proxy.gcodeLoader = new GcodeLoader({
    elId: "middle-container",
  });
  // Gcode,
  proxy.gcodeLoader.on("formatSlice", (model) => {
    // gcode
    state.formatSlice = model;
    // 
    state.layerCountMax = model.length - 1;
    // 
    state.stepCountMax = state.formatSlice[state.layerCountMax];
    console.log("", state.layerCountMax);
    console.log("", state.stepCountMax);
    // ,loading,Flag
    state.loading = false;
    setTimeout(() => {
      state.runFlag = false;
    }, 3000);
  });
  // 3D
  proxy.gcodeLoader.on("init", function () {
    console.log("worker");
    // this.renderFromUrl(
    //   "https://file-cdn-dev.creality.com/gcodePreview/fangKu.gcode"
    // );
  });
});
</script>
<style lang="scss" scoped>
.gcodeManner {
  padding-top: 70px;
  .container-main {
    position: relative;
    min-width: 1200px;
    width: 97.8%;
    height: 840px;
    margin: auto;
    @include theme_layout();
    .head-show {
      width: 100%;
      height: 41px;
      display: flex;
      justify-content: space-between;
      background: #46464a;
      border-bottom: 1px solid #46464a;
      border-radius: 5px;
      .left-feature {
        padding: 12px 15px;
        font-size: 16px;
        font-weight: normal;
        color: #ffffff;
        span {
          padding-left: 5px;
        }
        .spe-icon {
          color: #dbdbe0;
        }
      }
    }
    .main-container {
      // width: 1200px;
      display: flex;
      justify-content: flex-start;
      .left-container {
        width: 40px;
        height: 515px;
        display: flex;
        align-items: center;
        padding: 60px 0px 0px 20px;
        :deep().el-slider {
          margin-top: 0;
          .el-slider__runway {
            background: #4f4f54;
            width: 10px;
            .el-slider__bar {
              background: #00a0e9;
              width: 10px;
            }
            .el-slider__button-wrapper {
              bottom: 78%;
              left: -8px;
              width: 20px;
              height: 20px;
              background: #ffffff;
              border: 3px solid #37373b;
              border-radius: 50%;
              .el-slider__button {
                background: none;
              }
            }
          }
        }
      }
      #middle-container {
        width: 52%;
        height: 605px;
        background: #37373b;
        border: 1px solid #4f4f54;
        border-radius: 5px;
        margin-top: 20px;
      }
      .right-container {
        width: 300px;
        height: 175px;
        .gcode-name {
          text-align: left;
          padding: 21px 0px 0px 20px;
        }
        .bottom-box {
          width: 300px;
          height: 100px;
          background: #37373b;
          border: 1px solid #4f4f54;
          border-radius: 5px;
          text-align: left;
          margin: 9px 0px 20px 20px;
          p {
            margin-top: 20px;
            padding-left: 20px;
          }
        }
        .bottom-btn {
          .el-button {
            width: 300px;
            height: 36px;
            border-radius: 5px;
            margin-left: 20px;
            margin-bottom: 20px;
          }
        }
      }
    }
    .bottom-container {
      width: 608px;
      margin: 20px 0px 0px 60px;
      .left-control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .spe-span {
          font-size: 14px;
          @include theme_font();
        }
        .slide-con {
          :deep().el-slider {
            width: 557px;
            .el-slider__runway {
              background: #4f4f54;
              .el-slider__bar {
                background: #00a0e9;
              }
            }
          }
        }
        .brInfo {
          position: absolute;
          left: 36.6%;
          font-size: 16px;
          @include theme_font();
        }
      }
    }
  }
}
</style>
